<template>
<div v-resize='onResize'>
  <v-layout row wrap class='pa-2'>
    <v-flex xs12 class='pb-2'>
      <AppTitle icon='touch_app' title='Simple Interactive Simulator' />
    </v-flex>

    <v-flex xs8 class='pr-2'>
      <v-layout row wrap>
        <v-flex xs12 class='pb-3'>
          <v-card style='min-height: 300px; max-height: 300px;'>
            <div style='position:relative;' class='my-auto' :style='weatherColors[dayNightNum][weatherNum]'>
              <v-img :src='bldgImgSrc' :lazy-src='bldgImgSrc' max-height='300' min-height='300' :aspect-ratio='3' :style='dayNightOpacity[dayNightNum]' style='position: relative;'>
                <template v-slot:placeholder>
                  <v-layout fill-height align-center justify-center class='ma-0'>
                    <v-progress-circular indeterminate color='primary' />
                  </v-layout>
                </template>
              </v-img>
              <div ref='waterEcharts'   style='position: absolute;' :style='waterStyle' />
              <div ref='sunEcharts'     style='position: absolute; top: 0; left: 0; height: 100%; width: 100%' />
              <div ref='weatherEcharts' style='position: absolute; top: 0; left: 0; height: 100%; width: 100%' />
            </div>
          </v-card>
        </v-flex>

        <v-flex xs12>
          <v-card style='min-height: 300px; max-height: 300px;'>
            <div style='position:relative;' class='my-auto' :style='weatherColors[dayNightNum][weatherNum]'>
              <v-img :src='bldgImgSrc2' :lazy-src='bldgImgSrc2' max-height='300' min-height='300' :style='dayNightOpacity[dayNightNum]' style='position: relative;'>
                <template v-slot:placeholder>
                  <v-layout fill-height align-center justify-center class='ma-0'>
                    <v-progress-circular indeterminate color='primary' />
                  </v-layout>
                </template>
              </v-img>
              <div ref='sunEcharts2'     style='position:absolute; top:0%; left:0%; height:100%; width:100%' />
              <div ref='weatherEcharts2' style='position:absolute; top:0%; left:0%; height:100%; width:100%' />
            </div>
          </v-card>
        </v-flex>
      </v-layout>
    </v-flex>

    <v-flex xs4 class='pl-2'>
      <v-layout row wrap>
        <v-flex xs12 class='pb-2'>
          <v-card class='px-3'>
            <v-subheader class='pl-0'>Cistern Water Level ({{ waterLevelPerc }}%)</v-subheader>
            <v-slider v-model='waterLevelPerc' :thumb-size='24' thumb-label='always' append-icon='zoom_in' prepend-icon='zoom_out' @click:append='zoomIn' @click:prepend='zoomOut' />

            <v-subheader class='pl-0'>Solar Power Strengh ({{ solarPower }})</v-subheader>
            <v-slider v-model='solarPower' :thumb-size='24' thumb-label='always' append-icon='zoom_in' prepend-icon='zoom_out' @click:append='zoomInSolar' @click:prepend='zoomOutSolar' :max='9' />

            <v-layout row>
              <v-flex xs4 class='pr-2'>
                <v-tooltip bottom color='primary'>
                  <v-select v-model='solarTrajNum' :items='solarTrajectoryItems' label='Sunshine' slot='activator' />
                  <span>Select solar wave trajectory</span>
                </v-tooltip>
              </v-flex>
              <v-flex xs4 class='px-2'>
                <v-tooltip bottom color='primary'>
                  <v-select v-model='dayNightNum' :items='dayNightItems' label='Day/Night' slot='activator' />
                  <span>Select day or night time</span>
                </v-tooltip>
              </v-flex>
              <v-flex xs4 class='pl-2'>
                <v-tooltip bottom color='primary'>
                  <v-select v-model='weatherNum' :items='weatherItems' label='Weather' slot='activator' />
                  <span>Select weather condition</span>
                </v-tooltip>
              </v-flex>
            </v-layout>
          </v-card>
        </v-flex>

        <v-flex xs12 class='pt-2'>
          <v-card>
            <v-data-table :headers='headers' :items='tableItems' class='elevation-1 text-xs-center' hide-actions>
              <template v-slot:items='props'>
                <td class='text-xs-left'>{{ props.item.name }}</td>
                <td class='text-xs-left'>{{ props.item.amount }}</td>
              </template>
            </v-data-table>
          </v-card>
        </v-flex>
      </v-layout>
    </v-flex>
  </v-layout>
</div>
</template>

<script>
import AppTitle    from '@/components/shared/Title'

// import energySankey from '@/components/subs/energySankey'
// import waterSankey from '@/components/subs/waterSankey'
// import color from '@/components/plugins/color'

//** static variables */
const symbol = 'path://m 77.107143,118.59523 0.75595,-32.883922 18.520837,26.080362 24.56845,-23.812504 -4.53572,32.505944 31.08854,-3.21279 -24.47395,20.97767 24.75744,24.94643 -34.01786,-4.15774 0.37798,34.39584 -21.166668,-27.21429 -24.568454,22.3006 5.669644,-31.37202 -30.238095,2.64583 23.434524,-21.16667 -22.67857,-24.19047 z'
const solarTrajectory = [[
  { coords: [[3, 10], [4, 8.2]]  },
  { coords: [[4, 10], [5, 8.2]]  },
  { coords: [[5, 10], [6, 8.2]]  },
  { coords: [[6, 10], [7, 8.2]]  },
  { coords: [[7, 10], [8, 8.2]]  },
  { coords: [[8, 10], [9, 8.2]]  },
  { coords: [[9, 10], [10, 8.2]] }
], [
  { coords: [[5, 10], [4, 8.2]]  },
  { coords: [[5, 10], [5, 8.2]]  },
  { coords: [[5, 10], [6, 8.2]]  },
  { coords: [[5, 10], [7, 8.2]]  },
  { coords: [[5, 10], [8, 8.2]]  },
  { coords: [[5, 10], [9, 8.2]]  },
  { coords: [[5, 10], [10, 8.2]] }
]]



export default{
  // components: { energySankey, waterSankey },
  data: () => ({
    rainTraj: [[], [], []],
    solarTrajectoryItems: [{ text: 'Uniform', value: 0 }, { text: 'Radial', value: 1 }],
    dayNightItems: [{ text: 'Day', value: 0 },   { text: 'Night', value: 1 }],
    weatherItems : [{ text: 'Shiny', value: 0 }, { text: 'Cloudy', value: 1 }, { text: 'Rainy', value: 2 }],
    bldgImgSrc : require('../assets/LBC1.png'),
    bldgImgSrc2: require('../assets/LBC2.jpg'),
    waterCollectionRate: 0,
    waterConsumptionRate: 5,
    elecConsumptionRate: 90,
    waterChart: null,
    sunChart: null,
    sunChart2: null,
    weatherChart: null,
    weatherChart2: null,
    waterLevelPerc: 50,
    solarPower: 8,
    solarTrajNum: 0,
    weatherNum: 0,
    dayNightNum: 0,
    weatherColors: [
      ['background-color:rgba(255,185,0,0.9)', 'background-color:rgba(20,20,155,1)', 'background-color:rgba(20,20,155,1)'],
      ['background-color:rgba(0,0,0,.9)', 'background-color:rgba(0,0,40,.9)', 'background-color:rgba(0,0,40,.9)'],
    ],
    dayNightOpacity: ['opacity:0.8', 'opacity:0.4'],

    headers: [
      { text: 'Metric', align: 'left', sortable: false, value: 'name' },
      { text: 'Amount', align: 'left', sortable: false, value: 'amount' },
    ],
    waterStyle: { top: '72.7%', left: '53.35%', width: '14%', height: '13.1%' },
  }),
  methods: {
    zoomOut() {
      this.waterLevelPerc = _.max([this.waterLevelPerc - 10, 0])
    },
    zoomIn() {
      this.waterLevelPerc = _.min([this.waterLevelPerc + 10, 100])
    },
    zoomOutSolar() {
      this.solarPower = _.max([this.solarPower - 1, 0])
    },
    zoomInSolar() {
      this.solarPower = _.min([this.solarPower + 1, 9])
    },
    refreshPowerSankey() {

    },
    onResize() {
      if(!this.waterChart || !this.sunChart || !this.weatherChart) return
      _.debounce(() => {
        this.waterChart.resize()
        this.sunChart.resize()
        this.sunChart2.resize()
        this.weatherChart.resize()
        this.weatherChart2.resize()
      }, 100)()
    },
  },
  watch: {
    waterOption(to) {
      this.waterChart.setOption(to)
    },
    sunOption(to) {
      this.sunChart.setOption(to)
      this.sunChart2.setOption(to)
    },
    weatherOption(to) {
      this.weatherChart.setOption(to)
      this.weatherChart2.setOption(to)
    }
  },
  computed: {
    waterOption() {
      return {
        series: [{ type: 'bar', data: [this.waterLevelPerc] }]
      }
    },
    weatherOption() {
      this.solarPower = [8, 4, 1][this.weatherNum]
      this.waterCollectionRate = [0, 0, 10][this.weatherNum]
      return {
        series: this.rainTraj[this.weatherNum]
      }
    },
    sunOption() {
      return {
        series: [{
          name: 'tail',
          type: 'lines',
          coordinateSystem: 'cartesian2d',
          zlevel: 1,
          effect: { show: true, period: 10 - this.solarPower, trailLength: .8, color: '#ffa022', symbolSize: 3 },
          lineStyle: { normal: { color: '#ffa022', width: 0, curveness: 0.05 } },
          data: solarTrajectory[this.solarTrajNum]
        }, {
          name: 'sun',
          type: 'lines',
          coordinateSystem: 'cartesian2d',
          zlevel: 2,
          symbolSize: 25,
          effect: { show: true, period: 10 - this.solarPower, trailLength: 0, symbol, symbolSize: 15 },
          lineStyle: { normal: { color: '#ffa022', width: 0, opacity: 0.6, curveness: 0.05 } },
          data: solarTrajectory[this.solarTrajNum]
        }]
      }
    },
    tableItems() {
      return [{
        name: 'Cistern level',
        amount: `${ this.waterLevelPerc } %`,
      }, {
        name: 'No rain duration',
        amount: `${ _.round(this.waterLevelPerc / 2) } days`,
      }, {
        name: 'Water collection rate',
        amount: `${ this.waterCollectionRate } GPM`,
      }, {
        name: 'Water consumption rate',
        amount: `${ this.waterConsumptionRate } GPM`,
      }, {
        name: 'Energy collection rate',
        amount: `${ this.solarPower * 12 } Kw`,
      }, {
        name: 'Energy consumption rate',
        amount: `${ this.elecConsumptionRate } Kw`,
      }]
    },
  },
  mounted() {
    const rainData1 = _.times(30, i => {
      const x = _.random(1, true) * 1.3
      return { coords: [[x, 10], [x + 0.2, 0]] }
    })
    const rainData2 = _.times(50, i => {
      const x = _.random(1, true) * 2.5 + 1.3
      return { coords: [[x, 10], [x + 0.2, 5]] }
    })
    const rainData3 = _.times(90, i => {
      const x = _.random(1, true) * 6.5 + 3.8
      return { coords: [[x, 10], [x + 0.2, 8.2]] }
    })

    const len = _.map([rainData1, rainData2, rainData3], _.size)
    const arr = _.flatten(_.map([rainData1, rainData2, rainData3], (n, i) => [n.splice(0, len[i] / 2), n.splice(len[i] / 2, len[i])]))

    this.rainTraj = [[], [], _.map(arr, (data, i) => ({
      name: 'tail', type: 'lines', coordinateSystem: 'cartesian2d', zlevel: 1,
      effect: { show: true, constantSpeed: !(i % 2) ? 40 : 60, trailLength: .3, color: 'blue', symbolSize: 1 },
      lineStyle: { normal: { color: 'blue', width: 0, curveness: 0 } },
      data,
    }))]

    const waterOption = {
      color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{ offset: 0, color: 'white' }, { offset: 1, color: '#0095CC' }]),
      grid : { bottom: 0, left: 0, top: 0, right: 0 },
      xAxis: { data: ['Water'], show: false },
      yAxis: { show: false, min: 0, max: 100 },
      ...this.waterOption
    }
    const sunOption = {
      grid : { top: 5 },
      xAxis: { type: 'value', show: false, min: 0, max: 10 },
      yAxis: { min: 0, max: 10, show: false },
      ...this.sunOption
    }
    const weatherOption = {
      grid : { top: 0, left: 0 },
      xAxis: { type: 'value', show: false, min: 0, max: 10 },
      yAxis: { min: 0, max: 10, show: false },
      series: this.rainTraj[this.weatherNum],
    }

    this.waterChart    = echarts.init(this.$refs.waterEcharts)
    this.sunChart      = echarts.init(this.$refs.sunEcharts)
    this.sunChart2     = echarts.init(this.$refs.sunEcharts2)
    this.weatherChart  = echarts.init(this.$refs.weatherEcharts)
    this.weatherChart2 = echarts.init(this.$refs.weatherEcharts2)

    this.waterChart.setOption(waterOption)
    this.sunChart.setOption(sunOption)
    this.sunChart2.setOption(sunOption)
    this.weatherChart.setOption(weatherOption)
    this.weatherChart2.setOption(weatherOption)

    //** eventBus
    this.$root.$on('updateWaterStyle', x => this.waterStyle = {
      top   : '72.7%',
      left  : !x ? '53.35%' : '54.35%',
      width : !x ? '14%'    : '17.7%',
      height: '13.1%',
    })
  },
  beforeDestroy() {
    echarts.dispose(this.waterChart)
    echarts.dispose(this.sunChart)
    echarts.dispose(this.sunChart2)
    echarts.dispose(this.weatherChart)
    echarts.dispose(this.weatherChart2)
  },
  components: {
    AppTitle
  }
}
</script>


<style lang='stylus' scoped>
table.v-table tbody td, table.v-table tbody th
  height: 41px !important
</style>

<style lang='stylus'>
table.v-table thead tr:not(.v-datatable__progress)
  height: 53px !important
</style>
